.qr-code-wrapper input {
  width: 100%;
  height: 40px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 0 10px;
  font-size: 16px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.qr-code-wrapper input:focus {
  outline: none;
}

.qr-code-wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 30px;
}

.qr-code-wrapper .download-btn {
  max-width: 300px;
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 5px;
  padding: 30px;
  font-size: 36px;
  margin-bottom: 10px;
  box-sizing: border-box;
  background-color: #185d8b;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.qr-code-wrapper .download-btn:hover {
  background-color: #0e3a55;
  transition: 0.3s ease-in-out;
}

@media screen and (max-width: 350px) {
  .qr-code-wrapper canvas {
    width: 100% !important;
    height: auto !important;
  }
}
